<template>
  <div class="device_container">
    <el-form
      :label-position="'right'"
      :model="ruleForm"
      ref="ruleForm"
      :inline="true"
    >
      <el-form-item
        label="状态:"
        prop='status'
      >
        <el-select
          v-model="ruleForm.status"
          placeholder="请选择"
          @change='selectChange'
        >
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          :loading="device.loading"
          @click="search"
        >搜索</el-button>

      </el-form-item>
    </el-form>
    <div>
      <el-table
        :data="device.channelConfig.channelList"
        border
        style="width: 100%"
      >
        <el-table-column
          prop="channelID"
          label="通道ID"
          min-width="150"
        >
         <template slot-scope="scope">
            <span
              class="device_id"
              @click='toChannel(scope.row.channelID)'
            >{{scope.row.channelID}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="deviceID"
          label="设备ID"
          min-width="150"
        >
        </el-table-column>

        <el-table-column
          prop="channelName"
          label="通道名称"
          min-width="150"
        >
        </el-table-column>
        <el-table-column
          prop="status"
          label="状态"
          min-width="150"
        >
          <template slot-scope="scope">
            <span>{{scope.row.status == 'ON' ? '在线' : '离线'}}</span>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination_view">
        <el-pagination
          background
          layout="prev, pager, next"
          :page-size='10'
          :total="device.channelConfig.totalCount"
          :current-page='ruleForm.page'
          @current-change='pageChange'
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import { deviceList } from '@/api/device'
import { createNamespacedHelpers, mapState } from "vuex";
let { mapActions } = createNamespacedHelpers("device");
export default {
  data () {
    return {
      options: [
        {
          value: 'ON',
          label: '在线'
        },
        {
          value: 'OFF',
          label: '离线'
        }
      ],
      ruleForm: {
        status: '',
        page: 1,
        limit: 10,
        deviceID: ''
      },
      loading: false,
    }
  },
  computed: {
    ...mapState(['device'])
  },
  created () {
    if (this.$route.query.deviceID) {
      this.ruleForm.deviceID = this.$route.query.deviceID
    }
    this.getDevice()
  },
  methods: {
    ...mapActions(['getChannelList']),
    selectChange (event) {
      // this.ruleForm.page=1
    },
    pageChange (event) {
      this.ruleForm.page = event
      this.getDevice()
    },
    search () {
      this.ruleForm.page = 1
      this.getDevice()
    },
    getDevice () {
      this.getChannelList(this.ruleForm)
    },
    toChannel (channel) {
      this.$router.push('/manager/video?channel=' + channel+'&deviceID='+ this.ruleForm.deviceID)
    }
  }
}
</script>
<style lang="css">
.device_container .el-form--inline .el-form-item {
  float: left;
}
.device_id {
  color: dodgerblue;
  cursor: pointer;
}
.pagination_view {
  margin-top: 10px;
}
</style>


